.settings{
    display:flex;
}

.settingsWrapper{
    flex:9;
    padding: 20px;
}
.settingsTitle{
    display:flex;
    align-items:center;
    justify-content: space-between;
}

.settingsUpdateTitle{
    font-size: 30px;
    margin-bottom: 20px;
    color: lightcoral;
}

.settingsDeleteTitle{
    color: red;
    font-size: 12px;
    cursor: pointer;
}
.settingsForm{
    display:flex;
    flex-direction: column;
}

.settingsForm > label{
    font-size: 20px;
    margin-top: 20px;
}

.settingsPPInput{
    width: 30%;
}

.settingsForm > input{
    color:gray;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 30px;
    border:none;
    border-bottom: 1px solid lightgray;
}

.settingsSubmitButton{
    align-self:center;
    width: 150px;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
    display:flex;
    align-items: center;
    justify-content: center;
}

.settingsSubmitButton:hover{
    background-color:rosybrown;
}
